.banner {
    color: #fff;
    object-fit: contain;
    height: 448px;
}

.banner-bg {
    padding-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}

.button {
    cursor: pointer;
    color: #fff;
    outline:  none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
}

.button:hover {
    background-color: #e6e6e6;
    color: #000;
    transition:  all 0.2s;
}

.fadebottom {
    height: 16.2rem;
    background: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111);
}